import Vue from 'vue'
import Router from 'vue-router'
import Goods from 'content/goods/Goods'
import Ratings from 'content/ratings/Ratings'
import Seller from 'content/seller/Seller'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'goods'
    },
    {
      path: '/goods',
      name: 'Goods',
      component: Goods
    },
    {
      path: '/ratings',
      name: 'Ratings',
      component: Ratings
    },
    {
      path: '/seller',
      name: 'Seller',
      component: Seller
    }
  ]
})



